@import url("https://fonts.googleapis.com/css?family=Noto+Sans+SC:300");
@import url("https://fonts.googleapis.com/css?family=Montserrat");
@keyframes enter {
  from {
    position: relative;
    opacity: 0;
    top: -0.5rem; }
  to {
    position: relative;
    opacity: 1;
    top: 0; } }

.loadernode-enter {
  opacity: 0; }
  .loadernode-enter-active {
    opacity: 1;
    transition: opacity 200ms; }

.loadernode-exit {
  opacity: 1; }
  .loadernode-exit-active {
    opacity: 0;
    transition: opacity 300ms; }

.blogContent {
  background-color: #f5f7f9; }
  .blogContent__contentHeader {
    position: relative;
    top: -0.2rem;
    height: 20rem;
    border-radius: 0 0 2rem 2rem;
    background-image: linear-gradient(to bottom, #6ba174, #6ba176, #6ba177, #6ba179, #6ba17a, #6ba17b, #6ba17c, #6ba17d, #6ba17e, #6ba17f, #6ba180, #6ba181); }
  .blogContent__headerInside {
    min-width: 20rem;
    max-width: 60rem;
    margin: 0 auto;
    padding: 2rem; }
    @media (max-width: 767px) {
      .blogContent__headerInside {
        padding: 1.5rem; } }
  .blogContent__title {
    text-transform: uppercase;
    color: white;
    text-shadow: 2px 2px 10px #0000002d;
    font-weight: 100;
    margin-bottom: 2rem; }
  .blogContent__subtitle {
    color: rgba(255, 255, 255, 0.582);
    font-weight: 100;
