    text-transform: capitalize; }
  .blogContent__tags {
    list-style: none;
    margin-top: 1.5rem; }
    .blogContent__tags li {
      display: inline-block;
      color: white;
      font-weight: 100;
      margin-right: 2rem;
      background-color: rgba(255, 255, 255, 0.2);
