      border-radius: 4px;
      box-shadow: 4px 4px 10px 1px rgba(0, 0, 0, 0.166); }
  .blogContent__markdown-body {
    font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, "Microsoft Yahei", "Hiragino Sans GB", "Heiti SC", "WenQuanYi Micro Hei", sans-serif;
    box-sizing: border-box;
    min-width: 20rem;
    max-width: 70rem;
    margin: 0 auto;
    padding: 4.5rem;
    padding-bottom: 6.5rem;
    position: relative;
    opacity: 0;
    top: -0.5rem;
    animation: enter 1s forwards;
    color: #808080; }
    @media (max-width: 767px) {
      .blogContent__markdown-body {
        padding: 1.5rem; } }
    .blogContent__markdown-body h1,
    .blogContent__markdown-body h2,
    .blogContent__markdown-body h3,
    .blogContent__markdown-body h4,
    .blogContent__markdown-body h5,
    .blogContent__markdown-body h6 {
      color: #636c7e; }
